.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* Our CSS  */
.banner-card {
  background-color: white;
  width: 100vw;
}

.banner-img {
  height: 70vh;
  width: 100%;
}

.banner-title {
  font-size: 30px;
  color: white;
  position: absolute;
  top: 19rem;
  left: 2rem;
}

.banner-text {
  color: white;
  position: absolute;
  top: 22rem;
  left: 2rem;
}

/* .trending {
} */

.movies-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 1rem;
}

.movie-card {
  width: 20vw;
  margin: 1rem;
}

.movie-img {
  height: 50vh;
  width: 100%;
}

.movie-title {
  color: white;
  position: absolute;
  left: .5rem;
  top: 1rem;
}

.movie-button {
  position: absolute; 
  top: 82%;
} 


.button-wrapper, .trending{
  display: flex;
  justify-content: center;
}

.pagination {
  display: flex;
  justify-content: center;
}

.spinner {
  display: flex;
  justify-content: center;
  align-items: center;
}

.tableContent {
  text-align: center;
}